import React from "react";
import styled from "styled-components";
import CW from "../public/icons/CodeWars.svg";

import ReverseRibbon from "../public/ReverseRibbon.png";

const Wrapper = styled.section`
  background-color: #231d1c;
  font-family: Scribble, "Segoe UI", serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 4px;

  img.ReverseRibbon {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 0 0;
    position: relative;
    top: 5px;
  }
  div.head {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
    height: 75px;
    font-size: 30px;
    color: white;
    @media (max-width: 700px) {
      flex-wrap: wrap;
      font-size: 20px;
    }
    @media (max-width: 500px) {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      flex-wrap: nowrap;
      height: auto;
      line-height: 2;
      font-size: 30px;
      margin: 20px 0;
    }
    div {
      a {
        color: white;
        div.button:hover {
          cursor: pointer;
          opacity: 0.9;
        }
      }
    }
  }
  div.icons {
    height: 40px;
    width: 325px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: start;
    margin-bottom: 30px;
    margin-top: 10px;
    @media (max-width: 700px) {
      flex-wrap: wrap;
      width: 300px;
    }
    div.icon {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: start;
      a {
        i {
          transform: scale(3);
          color: white;
        }
      }
      a:hover {
        opacity: 0.9;
      }
    }
  }
`;
const content = [
  { name: "HOME" },
  { name: "ABOUT" },
  { name: "PORTFOLIO" },
  { name: "CONTACTS" }
];



const Menu = () => {

  return (
    <Wrapper>
      <div className="head">
        {content.map(({ name }) => {
          return (
            <div key={name + name}>
              <a href={"#" + name}>
                <div className="button">{name}</div>
              </a>
            </div>
          );
        })}
      </div>
      <div className="icons">
        <div className="icon">
          <a href="https://github.com/Automedon">
            <i
              className="fa fa-github display-4 text-white"
              aria-hidden="true"
            />
          </a>
        </div>
        <div className="icon">
          <a href="https://www.linkedin.com/in/frontreactdeveloper/">
            <i
              className="fa fa-linkedin display-4 text-white"
              aria-hidden="true"
            />
          </a>
        </div>
        <div className="icon">
          <a href="https://www.codewars.com/users/Valefar">
            <img src={CW} style={{ width: "48px" }} alt="" />
          </a>
        </div>
      </div>
      <img className="ReverseRibbon" src={ReverseRibbon} alt="" />
    </Wrapper>
  );
};

export default Menu;
